	module.exports = {"data":{"talkJson":{"description":"Over the past 3 years the JVM has largely overtaken C++ as the platform of choice for writing high performance software. The JVM / Java can be found powering HFT systems, low latency distributed databases, and real-time analytics platforms in almost every enterprise. During this talk you will come to understand the Java memory and concurrency models, how they interact with modern architectures, and the implications for how you ought to design your software. I will share my experiences designing, scaling and tuning a massive scale real-time analytics system, both the failures and the successes.\n\nby Cliff Moon (@moonpolysoft) - Boundary\n\nCliff Moon is the founder and CTO of Boundary. Cliff has lead the building of Boundary's streaming\n￼￼￼￼￼￼￼￼￼￼￼￼￼￼\nanalytics technology, which processes over 2 trillion metrics per day. Previously, Cliff was a leading engineer at Powerset, which was acquired by Microsoft. While there, Cliff helped kick off the NoSQL movement with his open source Dynomite project.","meta":{"duration":"PT39M55S"},"presenter":{"name":null},"slug":"ludicrous-speed-designing-for-performance-on-the-jvm-by-cliff-moon","source":{"id":"X1rWgshr3PY","provider":"youtube"},"tag":["Java Virtual Machine (Video Game Platform)","Concurrent Computing (Programming Language Paradigm)"],"title":"\"Ludicrous Speed: Designing For Performance on the JVM\" by Cliff Moon"},"allTalkJson":null},"pathContext":{"slug":"ludicrous-speed-designing-for-performance-on-the-jvm-by-cliff-moon","tags":["Java Virtual Machine (Video Game Platform)","Concurrent Computing (Programming Language Paradigm)"]}}

